@import url(https://fonts.googleapis.com/css?family=Dosis|PT+Sans+Narrow);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }


.sheet {
  max-width: 1000px;
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  margin: 20px auto;
  padding: 20px 10px;
  background-image: url(/portfolio/static/media/back.8bc860df.png);
  background-size: contain;
  background-repeat: no-repeat;
  height: 1000px;
  border-radius: 5px;
  box-shadow: 0px 1px 10px 1px #aaa; }

.experience .experience-item {
  margin-bottom: 10px; }
  .experience .experience-item .experience-item--logo {
    width: 64px;
    height: 64px;
    position: relative;
    padding: 5px; }
    .experience .experience-item .experience-item--logo img {
      max-width: 100%;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      margin: auto; }
  .experience .experience-item .experience-item--content {
    padding-left: 20px;
    font-family: "Dosis", sans-serif; }
    .experience .experience-item .experience-item--content .experience-item--employer > p {
      margin: 0; }
    .experience .experience-item .experience-item--content .experience-item--employer .employer-name {
      font-weight: 700; }
    .experience .experience-item .experience-item--content .experience-item--employer .employer-location {
      font-size: 90%;
      font-style: italic;
      color: #555; }
    .experience .experience-item .experience-item--content .experience-item--employer-duration {
      margin-top: 5px;
      color: #333; }
      .experience .experience-item .experience-item--content .experience-item--employer-duration > p {
        margin: 0; }
  .experience .experience-item .experience-item--description {
    margin: 0;
    font-size: 18px;
    font-weight: 300;
    font-family: "PT Sans Narrow", sans-serif; }

.card {
  padding: 8px;
  background-color: #fff;
  border: 1px solid #ddd;
  margin-bottom: 15px;
  box-shadow: 0px 2px 4px -1px #aaa; }
  .card > .card-title h3 {
    margin-top: 5px;
    margin-bottom: 5px;
    font-size: 28px;
    color: #008080;
    text-transform: uppercase;
    font-family: "Dosis", sans-serif;
    padding-bottom: 10px;
    border-bottom: 1px solid #ddd; }

.education .education-item {
  margin-bottom: 10px; }
  .education .education-item .education-item--content {
    padding-left: 20px;
    font-family: "Dosis", sans-serif; }

